.header{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 1rem;
    background: white;
    box-shadow: 0 .04rem .08rem 0 rgba(0,0,0,.12), 0 0 6px 0 rgba(0,0,0,.04);
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 10;
}
.header>div{
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.header .left,.header .right{
    width: .6rem;
}
.header .title{
    flex: 1 1;
    font-size: .32rem;
}
.commentList {
  background: white;
  padding: .2rem; }
  .commentList .commentItem:last-child:after {
    width: 0;
    height: 0; }
  .commentList .commentItem {
    width: 100%;
    height: auto;
    position: relative;
    background: white;
    color: gray;
    padding-bottom: .1rem; }
    .commentList .commentItem:after {
      content: '';
      width: 200%;
      height: 1px;
      position: absolute;
      bottom: 0;
      transform: scale(0.5);
      transform-origin: 0;
      background-color: gray; }
    .commentList .commentItem .userInfo {
      height: .8rem;
      line-height: .8rem;
      font-size: .24rem;
      display: flex;
      justify-content: center;
      align-items: center; }
      .commentList .commentItem .userInfo div:nth-child(1) {
        flex: 1 1;
        color: black; }
      .commentList .commentItem .userInfo div:nth-child(2) {
        width: auto; }
    .commentList .commentItem .userComment {
      line-height: .6rem;
      font-size: .3rem; }
    .commentList .commentItem .commentPicList {
      display: flex; }
      .commentList .commentItem .commentPicList img {
        display: block;
        width: 100px;
        height: 100px;
        box-sizing: content-box;
        margin-right: .1rem; }

.topicDetail {
  width: 100%;
  overflow-x: hidden; }

.commentWrap {
  width: 100%;
  height: auto;
  background: white;
  margin-top: .2rem; }
  .commentWrap .titleLine {
    width: 100%;
    height: 1rem;
    position: relative;
    display: flex;
    padding: 0 .2rem; }
    .commentWrap .titleLine:after {
      content: '';
      width: 200%;
      height: 1px;
      position: absolute;
      bottom: 0;
      transform: scale(0.5);
      transform-origin: 0;
      background-color: gray; }
    .commentWrap .titleLine div.titleName {
      flex: 1 1;
      height: 100%;
      line-height: 1rem;
      font-size: .3rem; }
    .commentWrap .titleLine div.titleIcon {
      width: 1rem;
      height: auto;
      line-height: 1rem;
      text-align: right;
      font-size: .4rem; }
  .commentWrap .noComment {
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center; }
    .commentWrap .noComment .noCommentIcon {
      width: 2.2rem;
      height: auto; }
      .commentWrap .noComment .noCommentIcon img {
        width: 100%;
        padding: .2rem; }
      .commentWrap .noComment .noCommentIcon div {
        color: gray;
        font-size: .3rem;
        padding: 0 .2rem;
        text-align: center; }
  .commentWrap .moreComment {
    line-height: .8rem;
    text-align: center;
    display: block; }

.relateTopic {
  margin-top: .2rem;
  padding: 0 .2rem; }
  .relateTopic .relateTopicTitle {
    line-height: .8rem;
    text-align: center; }
  .relateTopic .relateTopicItem {
    background: white;
    padding: .2rem .2rem 0 .2rem;
    margin-bottom: .2rem;
    display: block; }
    .relateTopic .relateTopicItem img {
      width: 100%;
      height: 4rem; }
    .relateTopic .relateTopicItem div {
      line-height: 1rem;
      color: gray;
      font-size: .28rem; }

.imgLazyload.loading {
  opacity: 0.5; }

.imgLazyload.loadEnd {
  opacity: 1;
  transition: all 1s; }

